<template>
  <div class="about">
    <div class="site-content">
      <div class="content-warp">
        <div class="about-site about-info">
          <section-title><span>❤</span>网站介绍</section-title>
          <div class="info-card">
            <p><img src="../../public/111.jpg"><img></p>
            <p>做一个dapp网站</p>
            <div v-if="tronWeb!==null">
              <p>你好：{{ walletAddress }}</p>
            </div>
            <div v-if="tronWeb===null">
              <el-button @click="linkWallet">连接钱包</el-button>
            </div>
            <p>简介有这么长。。。。。。。。。。。。。</p>
            <p>接下来。。</p>
            <p>路途遥远。。</p>
            <p>1.测试一个y智能合约</p>
            <p>2.一个未知的DAPP测试并上线</p>
            <p>3.研发一个区块链分布式项目</p>
            <p>4.区块链分布式程序节点测试</p>
            <p>5.区块链分布式项目智能合约研究</p>
            <p>6.区块链分布式项目对接打通波场</p>
            <p>7.【S.T.A.Y. (Delta Heavy Tribute)】</p>
            <br />
            <div>
              <div class="form-item">
                <!-- <label>已确认合约在代码中写好，准备部署</label><br/> -->
                <!-- <button @click="deploy_contract()">点击提交</button> -->
              </div>
            </div>
            <br />
            <div>
              <div class="form-item">
                <!-- <label>测试执行智能合约方法</label><br/> -->
                <!-- <button @click="triggercontract()">点击执行</button>
								<button @click="call()">点击查询</button> -->
              </div>
            </div>
          </div>

          <el-row>
            <el-button>操作：</el-button>
            <el-button type="primary" @click="clickLinkWallet"
              >获取钱包信息</el-button
            >
            <el-button type="success" @click="clickGetBalance"
              >获取钱包余额</el-button
            >
          </el-row>

           <el-row style="top: 20px; ">
            <el-button>其他网站：</el-button>
            <el-button><a href="https://bor233.cn">服务器主站</a></el-button>
            <el-button><a href="https://index.bor233.cn">几乎永久的主页</a></el-button>
          </el-row>

          <el-dialog
            title="tronlink钱包主网和侧链信息"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose"
          >
            <div>钱包地址：{{ walletAddress }}</div>
            <br />
            <div>{{ md }}</div>
            <br />
            <div>{{ sd }}</div>
            <br />
            <div>{{ ml }}</div>
            <br />
            <div>{{ sl }}</div>
            <br />
            <span slot="footer" class="dialog-footer">
              <!-- <el-button @click="dialogVisible = false">取 消</el-button> -->
              <el-button type="primary" @click="dialogVisible = false"
                >确 定</el-button
              >
            </span>
          </el-dialog>

          <el-dialog
            title="钱包余额"
            :visible.sync="dialogVisible2"
            width="30%"
            :before-close="handleClose"
          >
            <div>{{ balance1 }}</div>
            <br />
            <div>{{ balance2 }}</div>
            <span slot="footer" class="dialog-footer">
              <el-button type="primary" @click="dialogVisible2 = false"
                >确 定</el-button
              >
            </span>
          </el-dialog>

          <div class="about-me about-info" style="margin-top: 60px;">
            <section-title id="Guestbook"><span>❤</span>给我留言</section-title>
            <div class="info-card">
              <div class="contactForm">
                <div class="form-item">
                  <label for="mail">邮箱</label>
                  <input class="v" type="email" name="mail" id="mail" />
                </div>
                <div class="form-item">
                  <label for="content">内容</label>
                  <textarea class="v" id="content" name="content"></textarea>
                </div>
                <div class="form-item">
                  <label></label>
                  <button @click="open">提交</button>
                </div>
              </div>
            </div>
          </div>
