</template>
<script>
import sectionTitle from "@/components/section-title";
import { getTime, getTimeInterval } from "@/utils";
// import Quote from "@/components/quote";
// import {fetchFriend} from '../api'
export default {
  name: "index",
  data() {
    return {
      dialogVisible: false,
      dialogVisible2: false,
      tronlink:null,
      tronWeb:null,
      sunWeb:null,
      list: [],
      walletAddress: null,
      requestAccountsResponse: {
        code: 0, // 200：ok，4000：在队列中，不需要重复提交， 4001：user rejected
        message: "",
      },

      ml: "",
      md: "",
      sl: "",
      sd: "",
      balance1: "",
      balance2: "",
    };
  },
  components: {
    // Quote,
    sectionTitle,
  },
  methods: {
    async linkWallet() {
      var obj = setInterval(async () => {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          clearInterval(obj);
          this.requestAccountsResponse = await window.tronLink.request({
            method: "tron_requestAccounts",
          });
          this.tronlink = await window.tronLink;
          console.log(this.tronlink);
          this.tronWeb = await this.tronlink.tronWeb;
          console.log(this.tronWeb);
          this.sunWeb = await this.tronlink.sunWeb;
          console.log(this.sunWeb);
      
          this.walletAddress = this.tronWeb.defaultAddress.base58;
          console.log(this.tronWeb.defaultAddress.base58);
          this.sunWeb.mainchain.trx
            .getBandwidth(this.walletAddress)
            .then((r) => {
              console.log("主网带宽剩余：" + r);
              this.md = "主网带宽剩余：" + r;
            });
          this.sunWeb.sidechain.trx
            .getBandwidth(this.walletAddress)
            .then((r) => {
              console.log("侧链带宽剩余：" + r);
              this.sd = "侧链带宽剩余：" + r;
            });

          //查询主网资源
          this.sunWeb.mainchain.trx
            .getAccountResources(this.walletAddress)
            .then((tr) => {
              let rr = { 地址: this.walletAddress };
              let tre = tr.EnergyUsed > 0 ? tr.EnergyUsed : 0;
              let trr = tr.EnergyLimit - tre;
              console.log("主网能量剩余：" + trr);
              rr["主网能量剩余"] = trr;

              //查询侧链资源
              this.sunWeb.sidechain.trx
                .getAccountResources(this.walletAddress)
                .then((sr) => {
                  let sre = sr.EnergyUsed > 0 ? sr.EnergyUsed : 0;
                  let srr = sr.EnergyLimit - sre;
                  console.log("侧链能量剩余：" + srr);
                  rr["侧链能量剩余"] = srr;
                  console.log(rr);

                  this.ml = "主网能量剩余：" + trr;
                  this.sl = "侧链能量剩余：" + srr;
                });
            });
        }
      }, 1);
    },
    //查询钱包余额
    getBalance() {
      var obj = setInterval(async () => {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          clearInterval(obj);
          this.requestAccountsResponse = await window.tronLink.request({
            method: "tron_requestAccounts",
          });
          this.tronlink = await window.tronLink;
          console.log(this.tronlink)
          this.tronWeb = await this.tronlink.tronWeb;
          this.sunWeb = await this.tronlink.sunWeb;
          //当前连接的钱包地址获取 window.tronWeb.defaultAddress.base58
          let b1 = await this.tronWeb.trx.getBalance();
          let b2 = await this.sunWeb.sidechain.trx.getBalance(
          );
          this.balance1 = "主网balance：" + b1;
          this.balance2 = "侧链balance：" + b2;
          console.log("主网balance：", this.balance1);
          console.log("侧链balance：", this.balance2);
        }
      }, 1);
    },
    clickGetBalance(){
      this.dialogVisible2=true;
      this.getBalance();
    },
    clickLinkWallet(){
      this.dialogVisible=true;
      this.linkWallet();
    },
    open() {
      this.$alert("没有任何消息", "糟糕！！！", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.$message({
            type: "info",
            message: "糟糕！！！",
          });
        },
      });
    },
    // deploy_contract() {
    //   // new TronService().bushu()
    // },

    // triggercontract(){
    // 	new TronService().zhixing()
    // },

    // call(){
    // 	new TronService().chaxun()
    // },

    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
  },
 mounted() {
    // this.linkWallet();
    // this.getBalance();
  },
};
</script>
<style scoped lang="less">
.about {
  padding-top: 40px;
}

.content-warp {
  margin-top: 80px;

  .about-info {
    margin: 30px 0;

    span {
      color: red;
      margin-right: 10px;
    }

    .info-card {
      min-height: 100px;
      padding: 20px;
      border-radius: 3px;
      margin: 30px 0 50px 0;
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      p {
        line-height: 1.7rem;
      }
    }
  }
  .contactForm {
    width: 100%;
    padding: 20px;
    .form-item {
      align-items: center;
      display: flex;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      label {
        width: 80px;
      }
      .v {
        min-height: 40px;
        line-height: 20px;
        border-radius: 3px;
        padding: 2px 10px;
        outline: none;
        border: 1px solid #8fd0cc;
        width: 100%;
        resize: vertical;
      }
      button {
        width: 100px;
        height: 40px;
        border-radius: 3px;
        outline: 0;
        border-style: none;
        cursor: pointer;
        background-color: #409eff;
        color: white;
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
}

/*******/
@media (max-width: 800px) {
  .content-warp {
    margin-top: 0;
  }
}
</style>
